body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.menu {
    background: url(/static/media/ipod.b2c485c1.png);
    height: 530px;
    background-repeat: no-repeat;
    width: 300px;
    background-size: cover;
    margin: 0 auto;
    padding: 25px;
}

.menu ul {
    margin-top: 0px;
    list-style: none;
    text-align: left;
    font-size: 20px;
    -webkit-padding-start: 2px;
    width: 150px;
    background: white;
}

.menu ul > li {
    padding: 10px;
    cursor: default;
}
.menu ul > li.highlight {
    background-color: blue;
    color: white;
}

h3 {
    text-align: left;
    padding-left: 10px;
    margin-top: 4px;
    margin-bottom: 0px;
    background: white;
    width: 142px;
    padding-top: 10px;
    padding-bottom: 16px;
}
.img-cover > img {
    display: block;
    width: 290px;
    height: 220px;
    margin-top: 4px;
}
.touch-region {
    padding: 68px;
    margin-top: 62px;
    height: 210px;
    width: 210px;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    position: relative;
}
.touch-region > span {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin: 0px auto;
    cursor: pointer;
    display: block;
}
.touch-region > i {
    position: absolute;
    width: 50px;
    height: 30px;
    left: 80px;
    top: 7px;
    cursor: pointer;
}
.ipod-bg {
    background: url(/static/media/ipod_bg.c2a2f90f.jpeg);
    margin-left: 5px;
    width: 290px;
}
